<template>
  	<div class="box_home" @click="showDetail">
          <img src="../../assets/images/first.jpg" alt="">
        <!-- <div class = "icon">
            <div class = "logo">
                
            </div>
            <div class = "wechat">

            </div>
        </div>
        <div class = "connect">

        </div> -->
  	</div>
</template>

<script>
export default {
  name: "Item",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
  	showDetail () {
  		this.$router.push('/series')
  	}
  }
};
</script>

<style lang="less">
.box_home {
  width: 80%;
  height: 90%;
  background: #26bbff;
  cursor: pointer;
  display: flex; /*这个写在这只是为了#center这几个字的垂直居中*/
  margin: 5%;
  justify-content: center;
}
.icon {
  width: 20%;
  height: 75%;
  display: flex;
  margin-top: 7%;
  margin-left: 9%;
  flex-wrap: nowrap;
  flex-direction: column;
  //   background: rgb(72, 151, 156);
}
.logo {
  width: 100%;
  height: 45%;
  display: flex;
  //   background: rgb(9, 119, 51);
  background-image: url(../../assets/images/Logo/logo.png);
}
.wechat {
  width: 100%;
  height: 45%;
  display: flex;
  margin-top: 10%;
  background: rgb(9, 119, 51);
}
.connect {
  width: 55%;
  height: 75%;
  display: flex;
  margin-top: 7%;
  margin-left: 9%;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(72, 151, 156);
}
</style>



