import Vue from 'vue'
import Router from 'vue-router'
import HomeIndex from '@/page/home/Index'
import SeriesIndex from '@/page/series/Index'
import DetailIndex from '@/page/detail/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homeindex',
      component: HomeIndex
    },
    {
      path: '/index',
      name: 'homeindex',
      component: HomeIndex
    },
    {
      path: '/series',
      name: 'seriesindex',
      component: SeriesIndex
    },
    {
      path: '/detail',
      name: 'detailindex',
      component: DetailIndex
    }
  ]
})



