<template>
    <div class="container" @mouseover="state_update()">
      <div class="back" @click="$router.push('/index')">
			    <img class="back-img" src="../../assets/images/tools/back.png" alt="">
		  </div>
      <div class="main">
       
        <Item index = "0"/>
        <Item index = "1"></Item>
        <Item index = "2"></Item>
        <Item index = "3"></Item>
        <Item index = "4"></Item>
        <Item index = "5"></Item>
     
      </div>
    </div>
</template>
<script>
import Item from "./Item";
export default {
  name: "Index",
  components: {
    Item
  },
  data() {
    return {
      lastTime: new Date().getTime(),
      currentTime: new Date().getTime(),
      timeOut: 30 * 1000,
      intervalId: 0
    };
  },
  methods: {
    state_update() {
      this.lastTime = new Date().getTime();
    },
    testTime() {
      this.currentTime = new Date().getTime(); //更新当前时间
      if (this.currentTime - this.lastTime > this.timeOut) {
        console.log("定时任务进行中。。。");
        this.$router.push("/index");
      }
    }
  },
  created() {
    this.lastTime = new Date().getTime();

    /* 定时器  间隔1秒检测是否长时间未操作页面  */
    this.intervalId = setInterval(() => {
      this.testTime();
    }, 2000);
  },
  beforeDestroy() {
    clearInterval(this.intervalId);
  }
};
</script>
<style>
.container {
  background: #fff;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #26bbff;
}
.main {
  width: 95%;
  height: 88%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #26bbff;
}
.back {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 96px;
  height: 84px;
  cursor: pointer;
}
.back-img {
  width: 96px;
  height: 84px;
}
</style>



