<template>
  	<div class="detail_home" >
          <img :src= "imgList[index]" class="img_detail" alt="">
  	</div>
</template>

<script>
export default {
  name: "Item",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      imgList: [require('../../assets/images/detail/d1.jpg'),require('../../assets/images/detail/d2.jpg'),
      require('../../assets/images/detail/d3.jpg'),require('../../assets/images/detail/d4.jpg'),
      require('../../assets/images/detail/d5.jpg'),require('../../assets/images/detail/d6.jpg')]
    };
  },
  props :['index']
};
</script>

<style lang="less">
.detail_home {
  width: 90%;
  height: 100%;
  background: #26bbff;
  cursor: pointer;
  display: flex; /*这个写在这只是为了#center这几个字的垂直居中*/
  margin: 5%;
  justify-content: center;
  align-items: center;
}
.img_detail {
  // border: 1px solid #666;
  // border-radius: 25px; /*圆边角*/
  // box-shadow: 5px 5px 5px #888888;
  max-width: 100%;
}
</style>


