<template>
  	<div class="box" @click="showDetail">
          <img :src= "imgList[index]" class="img" alt="">
        <!-- <div class = "icon">
            <div class = "logo">
                
            </div>
            <div class = "wechat">

            </div>
        </div>
        <div class = "connect">

        </div> -->
  	</div>
</template>

<script>
export default {
  name: "Item",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      imgList: [require('../../assets/images/series/s1.png'),require('../../assets/images/series/s2.png'),
      require('../../assets/images/series/s3.png'),require('../../assets/images/series/s4.png'),
      require('../../assets/images/series/s5.png'),require('../../assets/images/series/s6.png')]
    };
  },
  props: ["index"],
  methods: {
  	showDetail () {
      debugger
  		this.$router.push({path:'/detail',query: {index:this.index}})
  	}
  }
};
</script>

<style lang="less">
.box {
  width: 30%;
  height: 40%;
  // background: #26bbff;
  cursor: pointer;
  display: flex; /*这个写在这只是为了#center这几个字的垂直居中*/
  margin: 0.5%;
  justify-content: center;
  /*阴影位置*/
}
.img {
  border: 1px solid #666;
  border-radius: 25px; /*圆边角*/
  box-shadow: 5px 5px 5px #888888;
}
.icon {
  width: 20%;
  height: 75%;
  display: flex;
  margin-top: 7%;
  margin-left: 9%;
  flex-wrap: nowrap;
  flex-direction: column;
  //   background: rgb(72, 151, 156);
}
.logo {
  width: 100%;
  height: 45%;
  display: flex;
  //   background: rgb(9, 119, 51);
  background-image: url(../../assets/images/Logo/logo.png);
}
.wechat {
  width: 100%;
  height: 45%;
  display: flex;
  margin-top: 10%;
  background: rgb(9, 119, 51);
}
.connect {
  width: 55%;
  height: 75%;
  display: flex;
  margin-top: 7%;
  margin-left: 9%;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(72, 151, 156);
}
</style>



