<template>
    <div class="container_home">
      <div class="main_home">
        <Item ></Item>
      </div>
    </div>
</template>
<script>
import Item from "./Item";
export default {
  name: "Index",
  components: {
    Item
  },
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
  	showDetail () {
  		this.$router.push('/series')
  	}
  }
};
</script>
<style>
.container_home {
  background: #fff;
  height: 100%;
  display: flex;
  justify-content: center;
}
.main_home {
  width: 80%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #fff;
  margin-top: 5%;
}
</style>



